import React, { useEffect } from "react";
import Layout from '../../../layout';
import { Grid, Button } from "../../../style";
import Header from "./header";
import { useSelector } from "react-redux";
import Chart from '../../../components/chart';
import { useMetricData } from '../../../hooks/metrics';
import useTimeseries from '../../../hooks/timeseries';
import charts from './chart.json';
import { defaultInterval } from '../../../utils/time';
import { Modal } from "platyplex_ui";
import { Editor } from "./editor";
import { TimeSeries } from '../../../model/domain/metrics';

const ts = (timeSeriesQuery: TimeSeries.PartialQuery | any) => ({
    ...timeSeriesQuery,
    interval: defaultInterval()
})

const Analytics = ({ switches }: { switches: JSX.Element }) => {
    const eId = useSelector((state: any) => state.user.enterprise.id);

    const { metricDataStatus: revenueMetricDataStatus, setMetricDataStatus } = useMetricData({
      timeseries: ts(charts[0].revenueTimeSeriesQuery),
      eId
    });
    
    const { metricDataStatus: transactionMetricDataStatus } = useMetricData({
      timeseries: ts(charts[0].transactionTimeSeriesQuery),
      eId
    });

    const { metricDataStatus: ms2 } = useMetricData({
        timeseries: ts(charts[1].timeSeriesQuery),
        eId
    });
 
  const EditModal = Modal({
    title: "Dynamic Pricing",
    message: <Editor add={()=>{}} close={() => EditModal.close()} />,
    hideCancel: true,
  });



    return (
        <Layout sidebar>
            <Layout.Top >
                {switches}
                <Header />
            </Layout.Top>
            <Layout.Bottom>
                <Grid placeItems="start stretch" padding="1em" height="100%">
                    <Grid cols="2" gridGap="0 1em">
                        <Button
                            placeSelf="end"
                            rounded
                            invert
                            shadow
                            onClick={EditModal.open}
                            style={{ gridColumn: '1/-1' }}
                            faIcon={`fa fa-edit`}
                        > Dynamic Pricings</Button>
                            <Chart.Multi
                                className='slide-up'
                                inChartLegend
                                fixedAlignment={'5m'}
                                metricDataStatuses={[revenueMetricDataStatus, transactionMetricDataStatus]}
                                {...charts[0] as any} />
                            <Chart
                                inChartLegend
                                isPercent={false}
                                className='slide-up'
                                metricDataStatus={ms2}
                                {...charts[1] as any} />
                    </Grid>
                </Grid>
            </Layout.Bottom>
        </Layout>
    );
};

export default Analytics;