import React from 'react';
import { Grid, Header as Text, List, Empty } from '../../../style';
import useGarages from '../../../hooks/garage';
import { useSelector } from 'react-redux';
import { Garage } from '../../../model/domain/garage';
import { ScoreCard } from '../../../components/score-card/score-card';
import { overflow } from '../../../utils/text';
import { moment } from '../../../utils/time';
import { invertedTxtColor } from '../../../config';
import PieChart from '../../../components/chart/components/graph/pie-chart'
import { getEnterpriseEvents, useSelectEnterpriseEvent } from '../model';
import * as formatter from '../../../utils/pretty_format';
import { Select } from 'antd';

const enterpriseEvents = getEnterpriseEvents();

const Header = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const loading = false;
    const { enterpriseEvent, selectEnterpriseEvent } = useSelectEnterpriseEvent();
    const { normal: revenueNormal, dynamic: revenueDynamic, total: revenueTotal } = enterpriseEvent.analytics.revenue;
    const garages = enterpriseEvent.reservationOccupancy;

    return (
        <Grid
            cols='5'
            style={{ gridColumn: '1/-1', marginBottom: '1em' }}
            width='100%'
            padding='1.5em 1.5em 0 1.5em'
        >
            <div
                id='header'
                className='grid'
                style={{
                    width: '100%',
                    margin: '0 0 .75em 0',
                    gridColumn: '1/-1',
                    gridTemplateColumns: '1fr auto'
                }}>
                <Select
                    style={{ width: '300px', placeSelf: 'start', color: 'white' }}
                    disabled={loading}
                    placeholder="Select"
                    onChange={eventId => selectEnterpriseEvent(eventId)}
                    value={enterpriseEvent.id}>
                    {enterpriseEvents.map(e => (
                        <Select.Option
                            key={e.id}
                            className='capitalize'
                            value={e.id}>
                            {e.name}
                        </Select.Option>))}
                </Select>
                <Text size={4.6} color={invertedTxtColor} className='hide-mobile'>  Updated at: {moment().format('hh:mm A')}</Text>
            </div>

            <ScoreCard title='Total Revenue' faIcon='fa fa-money' hoverLabel='Total Revenue' borderRight>
                <PieChart
                    height={130}
                    width={130}
                    text={formatter.formatCentsToDollars(revenueTotal)}
                    data={[
                        { label: `Normal Pricing (${formatter.formatCentsToDollars(revenueNormal)} total)`, value: revenueNormal },
                        { label: `Dynamic Pricing (${formatter.formatCentsToDollars(revenueDynamic)} total)`, value: revenueDynamic }
                    ]}
                />
            </ScoreCard>

            <ScoreCard title='Transactions' faIcon='fas fa-money-check-alt' hoverLabel='Revenue' borderRight>
                <span className='txt-lgx'>{enterpriseEvent.analytics.transaction}</span>
            </ScoreCard>

            <ScoreCard title='Reservation Occupancy' faIcon='fa fa-car' hoverLabel='Occupancy Count' span={2}>
                <Grid placeSelf='start' >
                    <Grid
                        customCols={loading ? '1fr' : '1fr  minmax(15em, auto)'}
                        placeItems='stretch'
                        width='100%'
                        color='white'
                        gridGap='.2em 1.5em'
                    >
                        <List
                            loading={loading}
                            empty={
                                <div style={{ gridColumn: '1/-1' }}>
                                    <Empty text={'No Garage Occupancy'} color="white" style={{ width: '7em', margin: '0 auto' }} />
                                </div>
                            }
                            pagination={{ items: 2 }}
                            list={garages}
                            map={(({ garageName, occupancy, staffs, expStaffs }) => {
                                return (
                                    <>
                                        <span className='txt-overflow'>{overflow(garageName, 23)}</span>
                                        <ScoreCard.PercentBar
                                            status
                                            percent={occupancy * 100}
                                            text={`${occupancy * 100 * 13} / ${1300}`}
                                        />
                                        <Grid
                                            mobileLayout={false}
                                            customCols='auto auto'
                                            style={{ gridColumn: '1/-1', marginBottom: '1em' }}
                                            width='100%'
                                            placeItems='stretch' >
                                            <span className='txt-left bold'>
                                                Estimated Staff: {staffs}
                                            </span>

                                            <span className='txt-right bold'>
                                                Actual Staff Assigned: {expStaffs}
                                            </span>

                                        </Grid>
                                    </>
                                )
                            })}
                        />
                    </Grid>
                </Grid>
            </ScoreCard>

        </Grid>);
}

export default Header;