import React, {  useMemo } from "react";
import Layout from '../../../layout';
import { Grid } from "../../../style";
import Header from "./header";
import { useSelector } from "react-redux";
import Chart from '../../../components/chart';
import { useMetricData } from '../../../hooks/metrics';
import {  useSelectEnterpriseEvent } from '../model';

const Analytics = ({ switches }: { switches: JSX.Element }) => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { enterpriseEvent: activeEvent } = useSelectEnterpriseEvent();
    const { enterpriseEvent: activeEvent2 } = useSelectEnterpriseEvent();
      
    const metricDataParam = (charts, i: number) => ({ timeseries: charts[i].timeSeriesQuery, interval: charts[i].timeSeriesQuery.interval, eId });

    const charts = activeEvent.getCharts();
    const metricDataStatuses = [
      useMetricData(metricDataParam(charts, 0)),
      useMetricData(metricDataParam(charts, 1)),
      useMetricData(metricDataParam(charts, 2)),
    ];

    useMemo(() => {
      const refreshMetricsData = (i: number) => {
        metricDataStatuses[i].refresh(charts[i].timeSeriesQuery);
      }
      [0, 1, 2].forEach(refreshMetricsData);
    }, [activeEvent.id]);

    return (
        <Layout sidebar>
            <Layout.Top >
                {switches}
                <Header />
            </Layout.Top>
            <Layout.Bottom>
                <Grid placeItems="start stretch" padding="1em" height="100%">
                    <Grid cols="3" gridGap="0 1em">
                        {charts.map((chart: any, i)=><Chart
                            isPercent={false}
                            className='slide-up'
                            inChartLegend
                            metricDataStatus={metricDataStatuses[i].metricDataStatus}
                            {...chart}
                        />)}
                    </Grid>
                </Grid>
            </Layout.Bottom>
        </Layout>
    );
};

export default Analytics;